<template>
  <v-container>
    <v-row align-content="center">
      <v-col cols="12" md="12">
        <h2>Device Info</h2>
        <v-divider></v-divider>
      </v-col>
      <v-col cols="12" md="12">
        <v-list>
          <v-list-item v-for="(val, key) in devices" :key="key">
            <v-list-item-content>
              <v-list-item-title
                :class="key === 'screen' && screenError ? 'error--text' : ''"
              >
                {{ val }}
              </v-list-item-title>
              <v-list-item-subtitle>{{ key }}</v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon v-if="key === 'screen' && screenError">
              <v-tooltip left>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-bind="attrs" v-on="on" color="error" right>
                    mdi-alert-octagon-outline
                  </v-icon>
                </template>
                <span>
                  You have to set the screen resolution to 1280x720.
                </span>
              </v-tooltip>
            </v-list-item-icon>
          </v-list-item>
        </v-list>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Home",
  components: {},
  computed: {
    devices() {
      return this.$store.state.adb.devices[0];
    },
    screenError() {
      return ["1280x720", "720x1280"].indexOf(this.devices.screen) > -1
        ? false
        : true;
    },
  },
};
</script>
